import React from 'react';
import Variable from './Variable';

const Variables = props => {
    let { variables,method } = props
        //Funo que lida com mudanas nas variveis.
        const deleteVar = xi => {
            if (variables.length > 2){
                variables.splice(xi, 1)
                variables.forEach( (vari,index) => vari.xi = index )
            }else{
                variables[xi].descripcion = ''
            }
        }
        //Funo responsvel por manipular as modificaes das restries.
        const handleChangesVar = (xi,desc) => {
            //Se a mudana  para deix-lo vazio, eliminamos a restrio. Mas, armazenamos o valor
            if (desc === '') { deleteVar(xi) }else{ variables[xi].descripcion = desc }
            //pedimos ao pai para guardar as alteraes
            props.handleVariables(variables);
        };
        //Funo responsvel por adicionar uma varivel, se necessrio.
    const handleNewsVar = () => {
        if (method === "simplex") {
            if( variables.length < 20 ){
                //Se o mtodo  Simplex, pode-se colocar mais de duas variveis.
                let counterWitheVar = variables.filter(element => element.descripcion.length === 0).length;
                //Se a quiantidade de variveis livre  igual a 0, coloca-se uma a mais.
                if (counterWitheVar === 0) {
                variables.push({ xi: variables.length, descripcion: "", coeficiente: "" });
                 props.handleVariables(variables);
                }
            }
        } else {
        //Se no, asseguramos que existam apenas duas, ento eliminamos a que est a mais.
            if (variables.length > 2) {
                variables.splice(2);
                props.handleVariables(variables);
            }
        }
    };
    handleNewsVar();
    return variables.map( variable => <Variable key={'VAR-'+variable.xi} handleChanges={handleChangesVar} variable={variable}/> )
}

export default Variables;